@font-face {
    font-family: 'Inter';
    font-style:  normal;
    font-weight: 900;
    font-display: swap;
    src: url("../fonts/Inter-Black.woff2")  format('woff2');
    src: url("../fonts/Inter-Black.woff")  format('woff');
    src: url("../fonts/Inter-Black.ttf")  format('truetype');
}

